import * as React from "react";
import ReactDOM from "react-dom";
export default class RenderInBody extends React.Component {
    constructor() {
        super(...arguments);
        this.renderLayer = () => {
            ReactDOM.render(React.createElement("div", Object.assign({}, this.props), this.props.children), this.popup);
        };
    }
    componentDidMount() {
        this.popup = document.createElement("div");
        document.body.appendChild(this.popup);
        this.renderLayer();
    }
    componentDidUpdate() {
        this.renderLayer();
    }
    componentWillUnmount() {
        ReactDOM.unmountComponentAtNode(this.popup);
    }
    render() {
        // Render a placeholder
        return React.createElement("div", Object.assign({}, this.props, { children: null }));
    }
}



