import * as React from "react";
import DocumentMeta from "react-document-meta";
const styles = require("./styles/main.less");
export default class TopicMain extends React.Component {
    constructor() {
        super(...arguments);
        this.renderMainThread = () => {
            return null;
        };
        this.renderPostList = () => {
            return null;
        };
        this.renderPostBox = () => {
            return null;
        };
    }
    render() {
        const { store } = this.props;
        const { loading, topic } = store;
        if (loading) {
            return (React.createElement("div", { className: styles.topicLoading },
                React.createElement("i", { className: "el-icon-loading" })));
        }
        const meta = {
            title: `${topic.title}-Eleun Forum-Web development community,WordPress,PHP,Java,JavaScript`,
            description: topic.content.substr(0, 100),
            // canonical: "https://elune.me",
            meta: {
                charset: "utf-8",
                name: {
                    keywords: "Eleun,forum,wordpress,php,java,javascript,react"
                }
            }
        };
        return (React.createElement("main", { className: styles.mainView },
            React.createElement(DocumentMeta, Object.assign({}, meta)),
            React.createElement("div", { className: styles.mainWrapper },
                this.renderMainThread(),
                this.renderPostList(),
                this.renderPostBox())));
    }
}



